import React from 'react';
import Card from './components/Card';
import Columns from "./components/Columns";
import ProductDiagram from "./components/ProductDiagram";
import { Button, Tooltip, IconButton, Grid, Typography, Box, Stack } from '@mui/material';
import { PythonLogo } from "~/assets/Logo";
import { GitHub } from '@mui/icons-material';

const ReservationScraping = () => {

    const [isOpen, setIsOpen] = React.useState(false);
    const handleClickDetailBtn = () => {
        setIsOpen(true);
    }
    const handleClose = () => {
        setIsOpen(false);
    }

    const DetailDialog = () => {

        const Abstract = () =>
            <Grid container spacing={2} columns={{ xs: 12, sm: 12 }}>
                <Grid item xs={12} sm={6} sx={{ textAlign: "center" }}>
                </Grid>
                <Grid item xs={12} sm={6} sx={{ alignSelf: "center" }}>
                    <Typography
                        variant={"body1"}
                        gutterBottom
                    >
                        
                    </Typography>
                    <Typography
                        variant={"body1"}
                        gutterBottom
                    >
                        
                    </Typography>
                    <Typography
                        variant={"body1"}
                        gutterBottom
                    >
                        PythonseleniumLineNotifyAPILINE
                    </Typography>

                </Grid>
            </Grid>

        const Requirement = () => {
            const contents = [
                {
                    label: "", content:
                        <Typography>
                            <ul>
                                <li></li>
                                <li>HP</li>
                            </ul>
                        </Typography>
                },
                {
                    label: "", content:
                        <Typography>
                            <ul>
                                <li></li>
                            </ul>
                        </Typography>
                },
                {
                    label: "", content:
                        <Typography>
                            <ol>
                                <li>()</li>
                            </ol>
                        </Typography>
                },
                {
                    label: "", content:
                        <Typography>
                            <ul>
                                <li></li>
                            </ul>
                        </Typography>
                },
                {
                    label: "", content:
                        <Typography>
                            PythonceleniumLINELINENotifyAPI
                        </Typography>
                },
            ];
            return (
                <Columns contents={contents} />
            )
        }

        const Tech = () => {

            const contents = [
                {
                    label: "", content:
                        <Box display="flex" flexWrap={'wrap'}>
                            <PythonLogo />celenium, LINENotifyAPI
                        </Box>
                },
                { label: "", content: <> : 2<br /> : 1</> },
                { label: "", content: <> : 2021/06 ~<br /> : 3</> },
            ]
            return (
                < Columns contents={contents} />
            )
        }

        const contents = [
            { label: "ABSTRACT", content: <Abstract /> },
            { label: "REQUIREMENT", content: <Requirement /> },
            { label: "TECHS", content: <Tech /> }
        ]

        const Actions = () =>
            <>
                <Tooltip title="This is private repository." placement="top">
                    <IconButton
                        href={"https://github.com/oura-hideyoshi/check-reservation"}
                        target={"_blank"}
                    >
                        <GitHub />
                    </IconButton>
                </Tooltip>
            </>
        return (
            <>
                <ProductDiagram
                    title={"LINE"}
                    contents={contents}
                    actions={<Actions />}
                    onClose={handleClose}
                    open={isOpen}
                ></ProductDiagram>
            </>
        )
    }

    return (
        <>
            <Card
                title={""}
                content={",LINE."}
                actions={
                    <>
                        <Button onClick={handleClickDetailBtn}>DETAIL</Button>
                        <DetailDialog />
                    </>
                }
            >
            </Card>
        </>
    )
}
export default ReservationScraping