import React, { useState } from 'react';
import Card from './components/Card';
import ProductDiagram from "./components/ProductDiagram";
import Columns from './components/Columns';
import { Button, IconButton, Tooltip, Typography, Grid, Box, Stack } from "@mui/material";
import santaquestLogo from "~/assets/static/img/santaquest.png";
import { GithubLogo, JavaLogo, SpringbootLogo, HtmlLogo, CssLogo, MysqlLogo } from "~/assets/Logo";
import { GitHub, Link, Videocam } from '@mui/icons-material';

const SantaQuest = () => {

    const [isOpen, setIsOpen] = useState(false);
    const handleClickDetailBtn = () => {
        setIsOpen(true);
    }
    const handleClose = () => {
        setIsOpen(false);
    }

    const DetailDialog = () => {

        const Abstract = () =>
            <Grid container spacing={2} columns={{ xs: 12, sm: 12 }}>
                <Grid item xs={12} sm={6} sx={{ textAlign: "center" }}>
                    <img src={santaquestLogo} style={{ width: "min(100%, 300px)" }} ></img>
                </Grid>
                <Grid item xs={12} sm={6} sx={{ alignSelf: "center" }}>
                    <Typography
                        variant={"body1"}
                        gutterBottom
                    >
                        
                    </Typography>
                    <Typography
                        variant={"body1"}
                        gutterBottom
                    >
                        WebApp
                    </Typography>
                    <Typography
                        variant={"body1"}
                        gutterBottom
                    >
                        WebApp300
                    </Typography>
                </Grid>
            </Grid>

        const Requirement = () => {

            const contents = [
                {
                    label: "", content:
                        <Typography>
                            <ul>
                                <li></li>
                                <li>HTML</li>
                            </ul>
                        </Typography>
                },
                {
                    label: "", content:
                        <Typography>
                            <ul>
                                <li></li>
                                <li></li>
                                <li></li>
                                <li></li>
                            </ul>
                        </Typography>
                },
                {
                    label: "", content:
                        <Typography>
                            <ol>
                                <li>(~18)</li>
                                <li></li>
                                <li></li>
                            </ol>
                        </Typography>
                },
                {
                    label: "", content:
                        <Typography>
                            <ul>
                                <li>
                                    
                                </li>
                                <li>
                                    
                                </li>
                            </ul>
                        </Typography>
                },
                {
                    label: "", content:
                        <>
                            <Typography gutterBottom>
                                GPS
                            </Typography>
                            <Typography gutterBottom>
                                
                            </Typography>
                        </>
                }
            ]

            return <Columns contents={contents} />

        }

        const Tech = () => {

            const contents = [
                {
                    label: "", content:
                        <Box display="flex" flexWrap={'wrap'}>
                            <GithubLogo /><JavaLogo /><SpringbootLogo /><HtmlLogo /><CssLogo /><MysqlLogo />{"Tomcat, Apache"}
                        </Box>
                },
                { label: "", content: <> : <br /> : 6</> },
                { label: "", content: <> : 2021/12/01 ~ ( 2022/01/31)<br /> : 300</> },
                { label: "", content: "" }
            ]
            return (
                < Columns contents={contents} />
            )
        }

        const contents = [
            { label: "ABSTRACT", content: <Abstract /> },
            { label: "REQUIREMENT", content: <Requirement /> },
            { label: "TECHS", content: <Tech /> }
        ]
        const Actions = () =>
            <>
                <IconButton
                    href={"https://wsapp.cs.kobe-u.ac.jp/SandaQuest/index"}
                    target={"_blank"}
                >
                    <Videocam />
                </IconButton>

                <Tooltip title="This is private repository." placement="top">
                    <IconButton
                        href={"https://github.com/Kobe-Spiral-2021-GroupA/santa-sanda-stamp-rally"}
                        target={"_blank"}
                    >
                        <GitHub />
                    </IconButton>
                </Tooltip>
                <Tooltip title=" 3 x " placement="top">
                    <IconButton
                        href={"https://www.city.sanda.lg.jp/citysales/2021-santasanda.html"}
                        target={"_blank"}
                    >
                        <Link />
                    </IconButton>
                </Tooltip>
            </>
        return (
            <>
                <ProductDiagram
                    title={" ! "}
                    contents={contents}
                    actions={<Actions />}
                    onClose={handleClose}
                    open={isOpen}
                ></ProductDiagram>
            </>
        )
    }

    return (
        <Card
            title={" ! "}
            content={"WebApp."}
            img={santaquestLogo}
            actions={
                <>
                    <Tooltip title=": ~ 2022/1/31" placement="top" >
                        <Button href={"https://wsapp.cs.kobe-u.ac.jp/SandaQuest/index"} target={"_brank"}>PLAY</Button>
                    </Tooltip>
                    <Button onClick={handleClickDetailBtn}>DETAIL</Button>
                    <DetailDialog />
                </>
            }
        ></Card>
    )
}
export default SantaQuest