import React from 'react';
import Card from './components/Card';
import Columns from "./components/Columns";
import ProductDiagram from "./components/ProductDiagram";
import { Button, Tooltip, IconButton, Grid, Typography, Box, Stack } from '@mui/material';
import { NodeLogo, ReactLogo, MuiLogo, HerokuLogo } from "~/assets/Logo";
import { GitHub, Videocam } from '@mui/icons-material';

const MChord = () => {

    const [isOpen, setIsOpen] = React.useState(false);
    const handleClickDetailBtn = () => {
        setIsOpen(true);
    }
    const handleClose = () => {
        setIsOpen(false);
    }

    const DetailDialog = () => {

        const Abstract = () =>
            <Grid container spacing={2} columns={{ xs: 12, sm: 12 }}>
                <Grid item xs={12} sm={6} sx={{ textAlign: "center" }}>
                </Grid>
                <Grid item xs={12} sm={6} sx={{ alignSelf: "center" }}>
                    <Typography
                        variant={"body1"}
                        gutterBottom
                    >
                        (ex: Fm7#add911)PC
                    </Typography>
                    <Typography
                        variant={"body1"}
                        gutterBottom
                    >
                        ''
                    </Typography>
                    <Typography
                        variant={"body1"}
                        gutterBottom
                    >
                        
                    </Typography>
                    <Typography
                        variant={"body1"}
                        gutterBottom
                    >
                        MChord()
                    </Typography>

                </Grid>
            </Grid>

        const Requirement = () => {
            const contents = [
                {
                    label: "", content:
                        <Typography>
                            <ul>
                                <li>PC</li>
                                <li></li>
                                <li></li>
                                <li></li>
                            </ul>
                        </Typography>
                },
                {
                    label: "", content:
                        <Typography>
                            <ul>
                                <li></li>
                                <ul>
                                    <li></li>
                                    <li></li>
                                    <li></li>
                                </ul>
                                <li></li>
                                <ul>
                                    <li></li>
                                </ul>
                            </ul>
                        </Typography>
                },
                {
                    label: "", content:
                        <Typography>
                            <ul>
                                <li></li>
                                <li></li>
                                <li></li>
                            </ul>
                        </Typography>
                },
                {
                    label: "", content:
                        <Typography>
                            <ul>
                                <li>DaD</li>
                                <li></li>
                            </ul>
                        </Typography>
                },
                {
                    label: "", content:
                        <>
                            <Typography gutterBottom>
                                ReactReactReactNative
                            </Typography>
                            <Typography gutterBottom>
                                React
                            </Typography>
                        </>
                },
            ];
            return (
                <Columns contents={contents} />
            )
        }

        const Tech = () => {

            const contents = [
                {
                    label: "", content:
                        <Box display="flex" flexWrap={'wrap'}>
                            <NodeLogo /> <ReactLogo /> <HerokuLogo /><MuiLogo />TonalJS
                        </Box>
                },
                { label: "", content: <> : 2 ~<br /> : 1</> },
            ]
            return (
                < Columns contents={contents} />
            )
        }

        const contents = [
            { label: "ABSTRACT", content: <Abstract /> },
            { label: "RECUIREMENT", content: <Requirement /> },
            { label: "TECHS", content: <Tech /> }
        ]

        const Actions = () =>
            <>
                <Tooltip title="DEMO" placement="top">
                    <IconButton
                        href={"https://mchord.herokuapp.com/"}
                        target={"_blank"}
                    >
                        <Videocam />
                    </IconButton>
                </Tooltip>
                <IconButton
                    href={"https://github.com/oura-hideyoshi/Mchord"}
                    target={"_blank"}
                >
                    <GitHub />
                </IconButton>
            </>
        return (
            <>
                <ProductDiagram
                    title={"MChord"}
                    contents={contents}
                    actions={<Actions />}
                    onClose={handleClose}
                    open={isOpen}
                ></ProductDiagram>
            </>
        )
    }

    return (
        <>
            <Card
                title={"MChord()"}
                content={"React"}
                actions={
                    <>
                        <Button href={"https://mchord.herokuapp.com/"} target={"_blank"}>PLAY</Button>
                        <Button onClick={handleClickDetailBtn}>DETAIL</Button>
                        <DetailDialog />
                    </>
                }
            >
            </Card>
        </>
    )
}
export default MChord